<template>
   <div class="fill">
     
       <div>
         <el-form :inline="true" ref="add_data" :model="serch_data">

           <!-- 时间筛选  -->
           <el-form-item label="按时间筛选:">
             <el-date-picker
      v-model="serch_data.startTime"
      type="datetime"
      placeholder="选择开始时间"
      >
    
     </el-date-picker>
          --
        <el-form-item >
             <el-date-picker
      v-model="serch_data.endTime"
      type="datetime"
      placeholder="选择结束时间"
      >
       </el-date-picker>
       </el-form-item>
           <el-form-item>
             <el-button type="primary" icon="search" @click="handleSearch()">筛选</el-button>
           </el-form-item>
    
     
           </el-form-item>
           <el-form-item class="btnRight">
             <el-button
             v-if="user.jurisdiction=='manager'"
              type="primary" 
              @click="handleAdd()">添加</el-button>
           </el-form-item>
         </el-form>

       </div>
     <div class="t_from">
    <el-table
    :data="tableData"
    max-height="450"
    border
    style="width: 100%">
    <el-table-column
      type="index"
      align="center"
      label="序号"
      width="70">
    </el-table-column>
    <el-table-column
      prop="date"
      align="center"
      label="创建时间"
      width="180">
       <template slot-scope="scope">
        <i class="el-icon-time"></i>
        <span style="margin-left: 10px">{{ scope.row.date }}</span>
      </template>
    </el-table-column>
    <el-table-column
       prop="type"
        align="center"
       label="收支类型"
       width="252"
    >
    </el-table-column>
    <el-table-column
     align="center"
       prop="describe"
       label="描述"
       width="200"
    >
    </el-table-column>
     <el-table-column
      align="center"
       prop="expend"
       label="支出"
       width="170"
    >
     <template slot-scope="scope">
        
        <span style="color:#4EB807">{{scope.row.expend}}</span>
      </template>
    </el-table-column>
  
     <el-table-column
      align="center"
       prop="income"
       label="收入"
       width="170"
    >
    <template slot-scope="scope">
        
        <span style="color:#F5250E">{{scope.row.income}}</span>
      </template>
    </el-table-column>
    <el-table-column
      align="center"
       prop="cash"
       label="现金"
       width="170"
    >
    <template slot-scope="scope">
        
        <span style="color:#3319B7">{{scope.row.cash}}</span>
      </template>
    </el-table-column>
     <el-table-column
       align="center"
       prop="remark"
       label="备注"
       width="240"
    >
    </el-table-column>
     <el-table-column 
     align="center"
     label="操作"
     width="255"
     >
      <template slot-scope="scope">
        <el-button
        v-if="user.jurisdiction=='manager'"
          size="small"
          type="success"
          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        <el-button
        v-if="user.jurisdiction=='manager'"
          size="small"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
  <el-row>
    <el-col :span="24">
      <div class="page_tools">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page.sync="pageitem.page_index"
                :page-sizes="pageitem.page_sizes"
                :page-size="pageitem.page_size"
                :layout="pageitem.layout"
                :total="pageitem.total">
    </el-pagination>
 
      </div>
    </el-col>
  </el-row>
  </div>
  <Dialog :dialog="dialog" :formData="formData" @update="goprofiles"></Dialog>
  </div>
</template>
<script>
import Dialog from '../components/Dialog'
export default {
    name:"fundlist",
    components: {
      Dialog
    },
    data(){
        return{
            serch_data:{
              startTime:'',
              endTime:''
            },
            filtertable:[],//定义过滤数据
            tableData:[],
            allTableData:[],
            dialog:{
               show:false,
               title:"",
               option:"edit"
            }, 
            formData:{
          type:"",
          describe:"",
          income:"",
          cash:"",
          remark:"",
          expend:"" ,
          id:""  

           },
           pageitem:{
              page_index:1,//当前页码
              tatal:0,// 总数量
              page_size:5,//每页显示多少 
              page_sizes:[5,10,15,20], //选择每页显示多少
              layout:"total,sizes,prev,pager,next,jumper" //功能
           }
        }
        

    },
    computed: {
      user(){
        //获取用户信息
        return this.$store.getters.user
      }
    },
    created() {
            this.goprofiles()
        },
        methods:{
            goprofiles(){
                this.$axios("/api/profiles")
                .then(res=>{
                   this.allTableData=res.data;
                      //获取过滤数据
                   this.filtertable =res.data;
                   //设置分页
                   this.setPageitem();
                })
                .catch(err=>{
                    console.log(err)
                })
            },
            setPageitem(){
              //设置分页数据
            this.pageitem.total=this.allTableData.length;
            this.pageitem.page_index=1;
            this.page_size =5;
            //设置默认的分页数据
            this.tableData = this.allTableData.filter((item,index)=>{
              return index< this.pageitem.page_size
            })
            },
            handleEdit(index,row){
                //更改状态
                  this.dialog={
                      show:true,
                      title:"修改信息",
                      option:'edit'
                },
                this.formData={
                  type:row.type,
                  describe:row.describe,
                  income:row.income,
                  cash:row.cash,
                  remark : row.remark,
                  expend:row.expend,
                  id : row._id
                }
               

            },
            handleDelete(index,row){
               this.$axios.delete(`/api/profiles/delete/${row._id}`)
               .then(res=>{
                 this.$message({
                   message:"删除成功!!",
                   type:"success"
                 });
                 this.goprofiles();

               })
            },
            handleAdd(){
               this.dialog={
                    show:true,
                    title:"添加信息",
                    option:"add"
                },
                this.formData={
                  type:'',
                  describe:'',
                  income:'',
                  cash:'',
                  remark :'',
                  expend:'',
                  id : ''
                }
               this.dialog.show= true
            },
            handleSizeChange(page_size){
                   //更改每页显示多少
                this.pageitem.page_index=1,
                this.pageitem.page_size =page_size,
                this.tableData = this.allTableData.filter((item,index)=>{
              return index< page_size
            })   
            },
            handleCurrentChange(page){
              //获取当前页下表
            let index =this.pageitem.page_size *(page-1);
              //数据的总数
            let nums = this.pageitem.page_size * page ;
              //新的容器
            let tables = [];
            //遍历数据 从当前页开始,到总数
            for(let i=index; i<nums; i++){
                   if(this.allTableData[i]){
                    
                    tables.push(this.allTableData[i]);
                   
                   }
                    this.tableData = tables
            }

            },
            handleSearch(){
              //筛选功能
               
              if(!this.serch_data.startTime || !this.serch_data.endTime){
                this.$message({
                     message:"请选择时间",
                     type:"warning"
                })
                this.goprofiles();
                return;
              } 
                 //获取开始时间
              const sTime =this.serch_data.startTime.getTime();
                  //获取结束时间
             const eTime =this.serch_data.endTime.getTime();
                this.allTableData = this.filtertable.filter(item=>{
                    //创建获得的时间的属性
                    let date = new Date(item.date);
                    //  定义时间的值
                    let time =date.getTime();
                    //选择大于开始时间小于结束时间的值
                    return time>=sTime && time<= eTime;
                  }
                )
                //重新进行分页过滤
                   this.setPageitem();
                   
            }
        },
        
}
</script>
<style scoped>
.fill{
    width: 100%;
    height: 100%;
    padding: 16px;
    box-sizing: border-box;
}
.btnRight{
  float: right;
}
.page_tools{
  text-align: right;
}
</style>
