import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)


const types={
  SET_CONDITION:"SET_CONDITION",
  SET_USER:"SET_USER"
};
const state ={
  isAuthorization:false,
  user:{}
};
const getters ={
  isAuthorization: state=>state.isAuthorization,
  
  user:state => state.user
  
  
};
const mutations ={
  [types.SET_CONDITION](state,isAuthorization){
    if(isAuthorization){
      state.isAuthorization =isAuthorization
    }else{
      state.isAuthorization=false
    }
  },
  [types.SET_USER](state,user){
     if(user) {
       state.user=user
      }else{
        state.user={}
      }
  }
};
const actions ={
 setCondition:({commit},isAuthorization)=>{
   commit(types.SET_CONDITION,isAuthorization)
 },
 setUser:({commit},user)=>{
   commit(types.SET_USER,user)
 },
 clearcurrent:({commit})=>{
   commit(types.SET_CONDITION,false)
   commit(types.SET_USER,null)
 }
}
export default new Vuex.Store({
  state,
  getters,
  mutations, 
  actions
})
