<template>
    <div class="register">
        <section class="form_container">
            <div class="manage_tip">
                <span class="title">在线资金管理系统</span>
                 <el-form :model="registerUser"  :rules="rules" ref="registerForm" label-width="80px" class="registerForm">
                    <el-form-item label="用户名" prop="name">
                     <el-input type="text" v-model="registerUser.name" placeholder="请输入用户名"></el-input>
                    </el-form-item>
                    <el-form-item label="邮箱" prop="email">
                     <el-input type="email" v-model="registerUser.email" placeholder="请输入邮箱"></el-input>
                    </el-form-item>
                     <el-form-item label="密码" prop="password">
                     <el-input type="password" v-model="registerUser.password" placeholder="请输入密码"></el-input>
                    </el-form-item>
                    <el-form-item label="确认密码" prop="password1">
                     <el-input type="password" v-model="registerUser.password1" placeholder="请确认密码"></el-input>
                    </el-form-item>
                    <el-form-item label="选择身份" prop="identity">
                     <el-select v-model="registerUser.jurisdiction" placeholder="请选择注册身份">
                         <el-option label="管理员" value="manager"></el-option>
                         <el-option label="员工" value="employee"></el-option>
                     </el-select>
                    </el-form-item>

                    <el-form-item >
                    <el-button type="primary" class="submit_btn" @click="submitForm('registerForm')">注册</el-button>
  
                    </el-form-item>
                 </el-form>
                
            </div>
        </section>
        
    </div>
</template>
<script>
export default {
    name:"register",
    components:{},
    data(){
         //密码和确认密码一致的验证
        var validatePass2 = (rule, value, callback) => {
        if (value !== this.registerUser.password) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
        return{
            registerUser:{
                name:"",
                email:"",
                password:"",
                password1:"",
                jurisdiction:""
            },
            rules:{
                name:[
                    {
                        //是否必填
                        required:true,
                        //错误提示
                        message:"用户名不能为空",
                        //触发规则
                        trigger:"blur"
                    },
                    {
                        //验证输入长度                             
                        min:2,
                        max:15,
                        //错误提示
                        message:"长度在2到15个字符之间",
                        //触发规则
                        trigger:"blur"
                    }
                ],
                email:[
                     {
                        //是否必填
                        required:true,
                        //验证格式
                        type:"email",
                        //错误提示
                        message:"邮箱格式不正确",
                        //触发规则
                        trigger:"blur"
                    },
                    
                ],
                password:[
                    {
                       required:true,
                       message:"密码不能为空",
                       trigger:"blur" 
                    },
                    {
                        min:6,
                        max:18,
                        message:"密码长度在6-18个字符之间",
                        trigger:"blur"
                    }

                ],
                password1:[
                     {
                       required:true,
                       message:"确认密码不能为空",
                       trigger:"blur" 
                    },
                    {
                        min:6,
                        max:18,
                        message:"密码长度在6-18个字符之间",
                        trigger:"blur"
                    },
                    {   
                        //调用2次密码一致验证
                        validator:validatePass2,
                        trigger:"blur"

                    }

                ]
            }

        }
    },
    methods: {
         submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$axios.post('./api/users/register',this.registerUser)
            .then(res=>{
                this.$message({
                    message:"账号注册成功",
                    type:"success"
                })
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
        this.$router.push({path:"/login"})
      }
      
    }
};
</script>
<style scoped>
  .register{
      position: relative;
      width: 100%;
      height: 100%;
      background: url(../assets/bg1.jpg) no-repeat center center;
      background-size: 100% 100%;
  }
  .form_container{
      width:370px;
      height: 210px;
      position: absolute;
      top: 10%;
      left: 34%;
      padding: 25px;
      border-radius: 5px;
      text-align: center;

  }
  .form_container .manage_tip .title{
      font-family:"Microsoft YaHei";
      font-weight: bold;
      font-size: 26px;
      color: #fff;
  }
  .registerForm {
  margin-top: 20px;
  background-color: #fff;
  padding: 20px 40px 20px 20px;
  border-radius: 5px;
  box-shadow: 0px 5px 10px #cccc;
}
  .submit_btn{
      width: 100%
  }
  
</style>
