<template>
   <div class="diglog"> 
    <el-dialog :title='dialog.title'
    :visible.sync="dialog.show"
    :close-on-click-modal="false"
    :modal-append-to-body="false"
    :close-on-press-escape='false'
    >
    <div class="form">
      <el-form 
      ref="form"
      :model="formData"
      :rules="rules"
      label-width="120px"
      style="margin:10px;width:auto"
      >
      <el-form-item label="收支类型:">
        <el-select v-model="formData.type" placeholder="收支类型">
            <el-option v-for="(formtype,index) in type_list" :key="index" lebel="formtype" :value="formtype"></el-option >
        </el-select>
      </el-form-item>
      <el-form-item prop="describe" label="收支描述:">
          <el-input type="describe" v-model="formData.describe"></el-input>
      </el-form-item>
      <el-form-item prop="income" label="收入:">
          <el-input type="income" v-model="formData.income"></el-input>
      </el-form-item>
      <el-form-item prop="expend" label="支出:">
          <el-input type="expend" v-model="formData.expend"></el-input>
      </el-form-item>
      <el-form-item prop="cash" label="现金:">
          <el-input type="cash" v-model="formData.cash"></el-input>
      </el-form-item>
      <el-form-item prop="remark" label="备注:">
          <el-input type="remark" v-model="formData.remark"></el-input>
      </el-form-item>

      <el-form-item class="text_right">
      <el-button type="primary" @click="submitform('form')">确 定</el-button>
      <el-button type="warning" @click="dialog.show = false">取 消</el-button>
    
   </el-form-item> 
      </el-form>
    </div>       
</el-dialog>
</div>
</template>
<script>
export default {
  name:"Dialog",
      props:{
        dialog:Object,
        formData:Object
      },
      data(){
        return{
         
           type_list:[
             "提现",
             "手续费",
             "充值",
             "礼券",
             "转账"

           ],
           rules:{
             describe:[
               {required:true,message:"描述不能为空",trigger:"blur"}
             ],
             income:[
               {required:true,message:"收入不能为空",trigger:"blur",}
             ],
             cash:[
               {required:true,message:"现金不能为空",trigger:"blur"}
             ],
             expend:[
               {required:true,message:"支出不能为空",trigger:"blur"}
             ]
        }
        }
        
        
      },
      methods: {
        submitform(form){
          this.$refs[form].validate(
            validate=>{
              const url = 
               this.dialog.option == "add" ? "add" : `edit/${this.form.id}`;
              if(validate){
                this.$axios.post(`/api/profiles/${url}`,this.formData)
                .then(res=>{
                  this.$message({
                    message:"添加成功!!",
                    type:'success'
                  })
                  //隐藏对话框
                  this.dialog.show= false;
                  //刷新页面
                  this.$emit('update');
                })
              }
            }
          )
        }
      }
}
</script>


